.button-icon{
  display: flex;
  background: transparent;
  box-shadow: none;
  cursor: pointer;
  border: 0;
  padding: 0; 
}

.button-icon:hover {
  filter: brightness(0.7);
}

@media (min-width: 768px) and (min-height: 290px),
       (max-width: 768px) and (max-height: 290px) {
  .button-icon {
    right: calc(var(--position) + 10px);
    top: 10px;
  }
}

@media (max-width: 768px) and (max-height: 290px) {
  .button-icon {
    right: calc(var(--position) + 5px);
    top: 5rpx;
  }
}
